import React from 'react'
import { createStyles } from '@app/react/common/hooks/createStyles'
import { spacing, responsive } from '@instacart/ids-core'
import { useLang } from '@app/services/config/hooks'
import { useAuth } from '@app/react/common/hooks/useAuth'
import { Icon } from '@instacart/cocktail'
import { FulfillmentType } from '@app/types/config'

interface Props {
  onClickCTA: (event: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void
}

const useStyles = createStyles(theme => ({
  container: {
    borderBottom: `1px solid ${theme.colors.systemGrayscale20}`,
    padding: `${spacing.s12}px 0px`,
    textAlign: 'left',
    [responsive.only('c')]: {
      padding: `${spacing.s12}px ${spacing.s16}px`,
    },
  },
  cta: {
    ...theme.typography.bodyLarge1,
    color: theme.colors.brandPrimaryRegular,
    paddingBottom: spacing.s12,
  },
  description: {
    ...theme.typography.bodySmall2,
    color: theme.colors.systemGrayscale70,
  },
  iconWrapper: {
    marginRight: spacing.s12,
    paddingTop: 2,
  },
  notice: {
    background: theme.colors.systemGrayscale10,
    border: `2px solid ${theme.colors.systemGrayscale30}`,
    borderRadius: spacing.s8,
    display: 'flex',
    flexDirection: 'row',
    margin: `${spacing.s12}px 0px`,
    padding: spacing.s12,
  },
  title: {
    ...theme.typography.bodyMedium1,
  },
}))

const ProductUnavailableDisclaimer = ({ onClickCTA }: Props) => {
  const styles = useStyles()
  const auth = useAuth()
  const lang = useLang()
  const productNotAtStoreLang =
    auth.intent === FulfillmentType.Delivery
      ? lang.productNotAtStore.delivery
      : lang.productNotAtStore.pickup

  return (
    <div css={styles.container}>
      <div css={styles.notice}>
        <span css={styles.iconWrapper}>
          <Icon icon="icon-info-i" />
        </span>
        <div>
          <div css={styles.title}>{productNotAtStoreLang.title}</div>
          <div css={styles.description}>{productNotAtStoreLang.description}</div>
        </div>
      </div>
      <button onClick={onClickCTA} css={styles.cta}>
        {productNotAtStoreLang.cta}
      </button>
    </div>
  )
}

export default ProductUnavailableDisclaimer
